body {
  margin: 0;
  padding: 0;
  
}
.card:hover {
  position: relative;
  box-shadow: 3 3px 6px #999, 10 3px 10px #999;
  background-position: 50%; 
  width: 222px;
  margin: 50px 50px 3px 90px;
  border: 7px solid#ffc200; 
  float: center;
  background-color: #d80000;
  -webkit-transform: scale(1.05);
  transform: scale(1.05);
  box-shadow: 0 14px 28px rgba(0,0,0,.25), 0 10px 10px rgba(0,0,0,.22);
  -webkit-transition: .1s;
  transition: .1s;

}

.card {
  position: relative;
  margin: 1rem 0 1rem 0;
  background-color: black;
  -webkit-transition: box-shadow .001s;
  transition: box-shadow .001s;
  border-radius: 2px; 
}

.card-panel {
-webkit-transition: box-shadow .5s;
transition: box-shadow .5s;
padding: 10px;
margin: 1rem 0 1rem 0;
border-radius: 2px;
background-color:#65747f;

}

.container-fluid {
  height: 175px;
  background-image: url("https://pngimage.net/wp-content/uploads/2018/05/bokeh-effect-png-2.png")
}

.svg-inline--fa {
font-size: 100px;
}

.shake {
-webkit-animation: shake 0.15s;
        animation: shake 0.15s; 
-webkit-animation-iteration-count: 10; 
        animation-iteration-count: 10; 
}
@-webkit-keyframes shake {
0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
50% { -webkit-transform: translate(4px, 0px); transform: translate(4px, 0px); }
100% { -webkit-transform: translate(-4px, 0px); transform: translate(-4px, 0px); }
}
@keyframes shake {
0% { -webkit-transform: translate(0px, 0px); transform: translate(0px, 0px); }
50% { -webkit-transform: translate(4px, 0px); transform: translate(4px, 0px); }
100% { -webkit-transform: translate(-4px, 0px); transform: translate(-4px, 0px); }
}


ul.navList {
    display: flex;
    justify-content: space-around;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
}

.correct {
    -webkit-animation: growCorrect 0.5s;
            animation: growCorrect 0.5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.incorrect {
    -webkit-animation: growIncorrect 0.5s;
            animation: growIncorrect 0.5s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
}

.Score {
    font-weight: bolder;
    color: #67210e;
}

.logo {
    font-family: Arial, Helvetica, sans-serif;
    font-size: 35px;
    font-weight: bold;
    color: #d80000;
}

@-webkit-keyframes growCorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #058d00; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: rgb(6, 240, 104); }
}

@keyframes growCorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #058d00; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: rgb(6, 240, 104); }
}

@-webkit-keyframes growIncorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #e20e0e; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: rgb(252, 229, 22); }
}

@keyframes growIncorrect {
    0% {-webkit-transform: scale(1);transform: scale(1); color: white; }
    25% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    50% {-webkit-transform: scale(1.75);transform: scale(1.75); color: #e20e0e; }
    50% {-webkit-transform: scale(1.5);transform: scale(1.5) }
    100% {-webkit-transform: scale(1);transform: scale(1); color: rgb(252, 229, 22); }
}
.footer {
    background: #3d2722;
    height: 100px;
    color: #d80000;
    overflow: auto;
    display: flex;
    justify-content: space-around;
    font-size: calc(10px + 14 * (100vw - 320px)/ 800);
    position: absolute;
   
}
.bottom {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    background: #ffc200;
    padding: 8px;
    font-weight: 600;
}

.footer>.bottom>img {
    width: 50px;
    position: relative; 
}

